.container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  background: linear-gradient(180deg, #0a0a0f 0%, #0d1020 50%, #0a0a0f 100%);
}

/* Animated starfield/particles background */
.particles {
  position: absolute;
  inset: 0;
  background-image:
    radial-gradient(2px 2px at 20px 30px, rgba(255, 255, 255, 0.25), transparent),
    radial-gradient(2px 2px at 40px 70px, rgba(0, 212, 255, 0.35), transparent),
    radial-gradient(1px 1px at 90px 40px, rgba(168, 85, 247, 0.4), transparent),
    radial-gradient(2px 2px at 130px 80px, rgba(236, 72, 153, 0.3), transparent),
    radial-gradient(1px 1px at 160px 120px, rgba(255, 255, 255, 0.2), transparent),
    radial-gradient(2px 2px at 200px 50px, rgba(88, 101, 242, 0.35), transparent);
  background-size: 220px 180px;
  animation: particlesDrift 60s linear infinite;
  pointer-events: none;
  z-index: 0;
}

@keyframes particlesDrift {
  0% {
    transform: translateY(0) translateX(0);
  }

  100% {
    transform: translateY(-100px) translateX(50px);
  }
}

/* Primary animated glow orb - Enhanced */
.glow {
  position: absolute;
  top: -200px;
  left: 50%;
  transform: translateX(-50%);
  width: 900px;
  height: 900px;
  background: radial-gradient(circle,
      rgba(0, 212, 255, 0.12) 0%,
      rgba(88, 101, 242, 0.1) 30%,
      rgba(168, 85, 247, 0.08) 50%,
      transparent 70%);
  pointer-events: none;
  animation: orbPulseEnhanced 10s ease-in-out infinite;
  filter: blur(40px);
}

/* Secondary floating orb - Enhanced */
.glowSecondary {
  position: absolute;
  bottom: -300px;
  right: -200px;
  width: 700px;
  height: 700px;
  background: radial-gradient(circle,
      rgba(236, 72, 153, 0.1) 0%,
      rgba(168, 85, 247, 0.08) 40%,
      transparent 70%);
  pointer-events: none;
  animation: orbFloatEnhanced 15s ease-in-out infinite;
  filter: blur(60px);
}

/* Third accent orb */
.glowTertiary {
  position: absolute;
  top: 40%;
  left: -150px;
  width: 500px;
  height: 500px;
  background: radial-gradient(circle,
      rgba(34, 255, 136, 0.08) 0%,
      rgba(0, 212, 255, 0.06) 50%,
      transparent 70%);
  pointer-events: none;
  animation: orbFloatReverse 18s ease-in-out infinite;
  filter: blur(50px);
}

@keyframes orbPulseEnhanced {

  0%,
  100% {
    opacity: 0.7;
    transform: translateX(-50%) scale(1);
  }

  50% {
    opacity: 1;
    transform: translateX(-50%) scale(1.15);
  }
}

@keyframes orbFloatEnhanced {

  0%,
  100% {
    transform: translate(0, 0) scale(1) rotate(0deg);
  }

  33% {
    transform: translate(40px, -40px) scale(1.1) rotate(5deg);
  }

  66% {
    transform: translate(-30px, 30px) scale(0.95) rotate(-3deg);
  }
}

@keyframes orbFloatReverse {

  0%,
  100% {
    transform: translate(0, 0) scale(1);
  }

  50% {
    transform: translate(60px, -20px) scale(1.1);
  }
}

.main {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px 24px;
}

.content {
  text-align: center;
  max-width: 700px;
}

/* Staggered badge animation */
.badge {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  background: rgba(88, 101, 242, 0.1);
  border: 1px solid rgba(88, 101, 242, 0.3);
  border-radius: 50px;
  font-size: 14px;
  font-weight: 500;
  color: #7289da;
  margin-bottom: 32px;
  opacity: 0;
  animation: fadeInBlur 0.6s ease forwards;
  animation-delay: 0.1s;
}

@keyframes fadeInBlur {
  from {
    opacity: 0;
    filter: blur(10px);
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    filter: blur(0);
    transform: translateY(0);
  }
}

.badgeDot {
  width: 8px;
  height: 8px;
  background: #43b581;
  border-radius: 50%;
  animation: pulseLive 2s ease-in-out infinite;
}

@keyframes pulseLive {

  0%,
  100% {
    opacity: 1;
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(67, 181, 129, 0.4);
  }

  50% {
    opacity: 0.8;
    transform: scale(0.9);
    box-shadow: 0 0 0 8px rgba(67, 181, 129, 0);
  }
}

/* Staggered title animation */
.title {
  font-size: 64px;
  font-weight: 800;
  line-height: 1.1;
  margin-bottom: 24px;
  letter-spacing: -2px;
  opacity: 0;
  animation: fadeInBlur 0.7s ease forwards;
  animation-delay: 0.2s;
}

.titleAccent {
  background: linear-gradient(135deg, #00d4ff 0%, #5865f2 25%, #a855f7 50%, #ec4899 75%, #00d4ff 100%);
  background-size: 300% 300%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: gradientShift 5s ease infinite;
  text-shadow: 0 0 80px rgba(88, 101, 242, 0.5);
}

@keyframes gradientShift {

  0%,
  100% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }
}

/* Staggered description */
.description {
  font-size: 18px;
  line-height: 1.7;
  color: #a0a0b0;
  margin-bottom: 40px;
  max-width: 550px;
  margin-left: auto;
  margin-right: auto;
  opacity: 0;
  animation: fadeInBlur 0.7s ease forwards;
  animation-delay: 0.35s;
}

/* Features with stagger */
.features {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 48px;
}

.feature {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  font-size: 16px;
  color: #c0c0d0;
  opacity: 0;
  animation: slideInFeature 0.5s ease forwards;
}

.feature:nth-child(1) {
  animation-delay: 0.5s;
}

.feature:nth-child(2) {
  animation-delay: 0.6s;
}

.feature:nth-child(3) {
  animation-delay: 0.7s;
}

@keyframes slideInFeature {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.feature svg {
  width: 20px;
  height: 20px;
  color: #43b581;
  animation: checkBounce 0.4s cubic-bezier(0.34, 1.56, 0.64, 1) forwards;
  animation-delay: inherit;
  opacity: 0;
}

@keyframes checkBounce {
  0% {
    transform: scale(0);
    opacity: 0;
  }

  60% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
    opacity: 1;
  }
}

/* Premium Discord button with neon effects */
.discordButton {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 14px;
  padding: 20px 52px;
  background: linear-gradient(135deg, #5865f2 0%, #7289da 50%, #a855f7 100%);
  background-size: 200% 200%;
  color: white;
  font-size: 18px;
  font-weight: 600;
  border-radius: 18px;
  transition: all 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
  position: relative;
  overflow: hidden;
  box-shadow:
    0 8px 32px rgba(88, 101, 242, 0.4),
    0 0 0 1px rgba(255, 255, 255, 0.1) inset,
    0 0 60px rgba(168, 85, 247, 0.2);
  opacity: 0;
  animation: fadeInScale 0.5s ease forwards, buttonGradientShift 4s ease infinite;
  animation-delay: 0.85s, 0s;
}

/* Gradient overlay with glassmorphism */
.discordButton::before {
  content: '';
  position: absolute;
  inset: 0;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.2) 0%, transparent 50%);
  border-radius: inherit;
}

/* Animated shine sweep effect */
.discordButton::after {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.4), transparent);
  transition: left 0.6s ease;
}

.discordButton:hover::after {
  left: 100%;
}

.discordButton:hover {
  transform: translateY(-6px) scale(1.03);
  box-shadow:
    0 20px 60px rgba(88, 101, 242, 0.5),
    0 0 80px rgba(168, 85, 247, 0.35),
    0 0 0 2px rgba(255, 255, 255, 0.15) inset,
    0 0 100px rgba(0, 212, 255, 0.2);
}

.discordButton:active {
  transform: translateY(-2px) scale(0.98);
  transition: transform 0.1s ease;
}

.discordButton svg {
  width: 26px;
  height: 26px;
  position: relative;
  z-index: 1;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.2));
}

@keyframes fadeInScale {
  from {
    opacity: 0;
    transform: scale(0.9);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes buttonGradientShift {

  0%,
  100% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }
}

/* Disclaimer with fade */
.disclaimer {
  margin-top: 24px;
  font-size: 13px;
  color: #6b6b7b;
  opacity: 0;
  animation: fadeInBlur 0.5s ease forwards;
  animation-delay: 1s;
}

.footer {
  padding: 24px;
  text-align: center;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
  animation: fadeInBlur 0.5s ease forwards;
  animation-delay: 1.1s;
  opacity: 0;
}

.footer p {
  font-size: 13px;
  color: #6b6b7b;
}

@media (max-width: 768px) {
  .title {
    font-size: 40px;
    letter-spacing: -1px;
  }

  .description {
    font-size: 16px;
  }

  .discordButton {
    padding: 16px 32px;
    font-size: 16px;
  }
}